body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* my styles */
body{
  height:100%;
  background-image:url("https://github.com/Jonathan169/roboTakeover/blob/gh-pages/images/background.jpg?raw=true");
  background-repeat: repeat
}
.conatiner{
  height:100vh;
}
nav{
  height:10%;
  width:100%;
  color:white;
  background-color:#5133a4;
  -webkit-justify-content: center;
          justify-content: center;
  position: fixed;
  display:-webkit-flex;
  display:flex;
  overflow: hidden;
}
.navitems{
  font-size:4vh;
  font-weight: bold;
  margin-left: 3vw;
  margin-right:3vw;
}
ul{
  padding-top:1vh;
  height: 100%;
  display:-webkit-flex;
  display:flex;
  list-style-type: none;
}
.images{
  height:50%;
  width:80%;
  -webkit-justify-content: center;
          justify-content: center;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow:row wrap;
          flex-flow:row wrap;
  -webkit-align-items: center;
          align-items: center;
  overflow: hidden;
  padding:5%;
  position: relative;
  left:5%;
}
.imageCont{
  width:9rem;
  border:.25rem solid black;
  margin-left:5%;
  margin-right:5%;
}
.gimage{
  width:10rem;
  height:10rem;
}
.fontc{
  color:white;
}
.game{
  position: absolute;
  top:10%;
  text-align: center;
  width:100%;
}

